import React from "react";
import MetaTags from 'react-meta-tags';
import "./Styles.css";

const darkMode = () => {
  const body = document.querySelector("body");
  const dayNight = document.querySelector(".dayNight");
  body.classList.toggle("dark");
  dayNight.classList.toggle("active");
};

const Header = ({ menuToggler }) => {

  return (
    <header>
    <MetaTags>
            <meta name="description" content="LHCH is a subgroup around the iconic trait the Lazy Hat on the Lazy Cubs. LHCH runs the Lazy Pool Investment Club/DAO" />
            <title>Lazy Hat Cub House</title>
        <meta property="og:title" content="Lazy Hat Cub House" />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://lazyhatcubs.xyz/" />
        <meta property="og:image" content="https://lazyhatcubs.xyz/videos/roadmap-released-screenshot.jpg" />
          </MetaTags>

      <a href="/" className="logo">
        Lazy Hat <img src="/videos/cubslogo.png" alt="cubs" className="cubslogo" />
      </a>
      <div className="rightSide">
        {/*<div onClick={darkMode} className="btns dayNight">
          <ion-icon name="moon-outline"></ion-icon>
          <ion-icon name="sunny-outline"></ion-icon>
        </div>*/}
        <div onClick={menuToggler} className="btns menuToggle">
          <ion-icon name="menu-outline"></ion-icon>
          <ion-icon name="close-outline"></ion-icon>
        </div>
      </div>
    </header>
  );
};

export default Header;
